.breadcrumbs {
    margin-top: 60px;
    margin-left: -8.6px;
    line-height: 1.1;
    margin-bottom: 60px;
    width: 100%;

    @include breakpoint-lg {
        margin-bottom: 42px;
    }

    @include breakpoint-md {
        margin: 0;
        margin-bottom: 24px;
    }

    a {
        font-size: 16px;
        font-weight: 300;
        font-family: $font-light;
        color: $color-dark-grey;
        padding: 0 8.6px;

        @include breakpoint-md {
            padding: 0;
            font-size: 14px;
        }
    }

    span {
        color: $color-dark-green;
    }

    span.breadcrumb_last {
        font-size: 16px;
        padding: 0 8.6px;
        font-weight: 500;
        color: $color-purple;

        @include breakpoint-md {
            padding: 0;
            font-size: 14px;
        }
    }
}